@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
html,body,h1,h2{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}body{line-height:1}body{font-family:'Roboto', sans-serif;font-size:14px;font-weight:400}

.App{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.App__container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;min-height:100vh;width:75%}

.Header{display:-webkit-flex;display:flex;-webkit-flex-shrink:0;flex-shrink:0;padding-top:48px;padding-bottom:48px}

.Footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;-webkit-flex-shrink:0;flex-shrink:0;padding-top:30px;padding-bottom:30px;font-size:14px;font-weight:400}.Footer__text{font-size:14px;font-weight:400}.links__item{text-decoration:none;text-transform:uppercase;font-weight:700;letter-spacing:2px;color:#000000;margin-right:30px;transition:color 0.3s}.links__item:hover{color:#800505}.links__item:last-child{margin-right:0}

.container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-grow:1;flex-grow:1}.TokenForm{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;color:#000000}.TokenForm__title{font-size:64px;font-weight:500;margin-bottom:3px}.TokenForm__subtitle{font-weight:500;margin-bottom:24px}.TokenForm__form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start}.form__item{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:16px}.form__item__name{font-size:12px;color:#727272;margin-bottom:5px}.form__item__label{cursor:pointer}.form__item__select{width:-webkit-max-content;width:max-content;border:none}.form__item__checkbox{position:absolute;opacity:0;margin-right:10px;cursor:pointer}.form__item__text{position:relative;left:25px;font-weight:500}.form__item__checkbox:checked ~ .form__item__checkmark{background-color:#c50000;border:2px solid #bbbbbb}.form__item__checkbox:checked ~ .form__item__checkmark:after{display:block}.form__item__checkmark{position:absolute;height:13px;width:13px;background-color:#ffffff;border:1px solid #bbbbbb}.form__item__checkmark:after{content:"";position:absolute;display:none}.form__button{max-width:130px;min-height:36px;font-size:16px;font-weight:900;color:#ffffff;text-transform:uppercase;letter-spacing:0.04em;background-color:#c50000;border:none;border-radius:48px;cursor:pointer;transition:background-color 0.3s}.form__button:hover{background-color:#800505}.form__supply{position:relative;margin-bottom:60px}

.TokenFormSubmitted__item{font-size:18px;font-weight:400;margin-bottom:10px}.TokenFormSubmitted__item:last-child{margin-bottom:0}

.TokenFormInput{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:16px}.TokenFormInput__name{font-size:12px;color:#727272;margin-bottom:5px}.TokenFormInput__input{box-sizing:border-box;min-width:320px;max-height:30px;box-sizing:border-box;border:1px solid #bbbbbb;border-radius:8px;padding:8px}

.TokenFormRadio__container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.TokenFormRadio__item{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:16px}.TokenFormRadio__name{font-size:12px;color:#727272;margin-bottom:5px}.TokenFormRadio__input{opacity:0;position:fixed}.TokenFormRadio__input:focus+.TokenFormRadio__label{background-color:#c50000;color:#ffffff}.TokenFormRadio__input:checked+.TokenFormRadio__label{background-color:#c50000;color:#ffffff}.TokenFormRadio__label{padding:7px 12px;color:#000000;font-weight:500;cursor:pointer;border:1px solid #bbbbbb;border-radius:28px;margin-right:10px;transition:all 0.3s}.TokenFormRadio__label:hover{background-color:#800505;color:#ffffff}.TokenFormRadio__label:last-child{margin-right:0}

